;
export function defineModel(def) {
    return def;
}
export function startModel(model, state) {
    const data = {
        model,
        state,
        mutate(name, ...args) {
            const mutation = data.model.mutations[name];
            mutation(data.state, ...args);
        },
    };
    return data;
}
export function initModel(model) {
    const state = model.init();
    return startModel(model, state);
}
export function loadModel(model, saved) {
    const state = model.load(saved);
    return startModel(model, state);
}
export function wrapMutate(data, callback) {
    const original = data.mutate;
    data.mutate = (name, ...args) => {
        callback(original, data.state, name, ...args);
    };
}
